      cor_nome: {
        hex: "#CCB095",
      },
      cor_df: {
        hex: "#e0e1e2",
      },
      cor_arq: {
        hex: "#96989a",
      },
    };
  },
  computed: {
    estilo_nome() {
      return {
        fill: this.cor_nome.hex,
      };
    },
    estilo_df() {
      return {
        fill: this.cor_df.hex,
      };
    },
    estilo_arq() {
      return {
        fill: this.cor_arq.hex,
      };
    },
  },
  methods: {
    updateCorNome(value) {
      this.cor_nome = value;
    },
    updateCorDF(value) {
      this.cor_df = value;
    },
    updateCorArq(value) {
      this.cor_arq = value;
    },
    Redefinir(){
      this.cor_nome.hex = "#CCB095";
      this.cor_df.hex = "#e0e1e2";
      this.cor_arq.hex = "#96989a";
    }
  },
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.fil1 {
  fill: none;
}
.fil0 {
  fill: #606062;
}
.fil2 {
  fill: #e0e1e2;
  fill-rule: nonzero;
}
.fil4 {
  fill: #96989a;
  fill-rule: nonzero;
}
.cor_nome {
  fill: #ccb095;
  fill-rule: nonzero;
}
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



